body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Raleway-medium", "Arial", sans-serif;
  color: #333333;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

@font-face {
  font-family: "Raleway-medium";
  src: url("../fonts/Raleway-Medium.woff2") format("woff2"), url("../fonts/Raleway-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway-semibold";
  src: url("../fonts/Raleway-SemiBold.woff2") format("woff2"), url("../fonts/Raleway-SemiBold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway-bold";
  src: url("../fonts/Raleway-Bold.woff2") format("woff2"), url("../fonts/Raleway-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter-regular";
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter-medium";
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    padding-left: 24px;
    padding-right: 24px;
    width: 93%;
    max-width: 1140px;
  }
}
@media (min-width: 1140px) {
  .container {
    width: 1140px;
  }
}
.button {
  color: #ffffff;
  text-align: center;
  background-color: #5BC3E3;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 25px;
  border: 1px solid #5BC3E3;
  font-size: 14px;
  line-height: 14px;
}
.button:hover {
  color: #ffffff;
  background-color: #2EA0C3;
}
.button:active {
  background-color: #2EA0C3;
}

.button--light {
  background-color: rgba(91, 195, 227, 0.1);
  color: #5BC3E3;
  border: 1px solid rgba(91, 195, 227, 0.1);
}
.button--light:hover {
  color: #ffffff;
  background-color: #5BC3E3;
}
.button--light:active {
  background-color: #5BC3E3;
}

.button--transparent {
  background-color: #ffffff;
  color: #5BC3E3;
}
.button--transparent:hover {
  color: #ffffff;
  background-color: #5BC3E3;
}
.button--transparent:active {
  background-color: #5BC3E3;
}

.header {
  padding-top: 18px;
  padding-bottom: 18px;
}

.header--fixed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2000;
  background-color: #ffffff;
}

@media (max-width: 1139px) {
  .menu {
    position: relative;
  }
  .menu--closed .main-nav__list {
    display: none;
  }
  .menu--closed .menu__bottom {
    display: none;
  }
  .menu--opened .menu__bottom {
    display: flex;
    position: absolute;
    top: 59px;
    left: 0;
    height: calc(100vh - 71px);
    width: 100%;
    overflow: hidden;
    overflow-y: auto;
    padding-left: 15px;
    padding-right: 15px;
    box-sizing: border-box;
    flex-direction: column;
    justify-content: flex-start;
    z-index: 900;
    background-color: #ffffff;
  }
  .menu--nojs .main-nav__list {
    display: block;
  }
  .menu--nojs .menu__bottom {
    display: block;
  }
}
@media (min-width: 1140px) {
  .menu__top {
    padding-bottom: 21px;
  }
  .menu__bottom {
    padding-top: 25px;
    padding-bottom: 5px;
  }
}
.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-nav__logo {
  flex: 1 1 auto;
}

.main-nav__pesonal {
  margin-right: 19px;
}

.main-nav__toggle {
  background: url("../img/burger-icon.svg") no-repeat left center/35px 25px;
  display: block;
  height: 25px;
  width: 35px;
  padding: 0;
  border: none;
  transition: 0.2s;
  cursor: pointer;
  outline: 0;
}

.main-nav__toggle--active {
  background: url("../img/cross-icon.svg") no-repeat 10% center/25px 25px;
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  .main-nav__image {
    width: 184.36px;
    height: auto;
  }
  .main-nav__pesonal {
    margin-right: 26px;
  }
}
@media (min-width: 1140px) {
  .main-nav__feedback li {
    padding-top: 11px;
    padding-bottom: 11px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__logo {
    flex: 0 1 auto;
  }
  .main-nav__list {
    flex: 1 1 auto;
    padding-right: 15px;
    display: flex;
    justify-content: flex-end;
  }
  .main-nav__pesonal {
    margin-right: 0;
  }
}
.user {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
}

.user__link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #5BC3E3;
  border-radius: 50%;
}
.user__link::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-size: contain;
  width: 15.23px;
  height: 18px;
  background-image: url("../img/icon-user.svg");
}
.user__link:hover {
  color: #ffffff;
  background-color: #2EA0C3;
}
.user__link:active {
  background-color: #2EA0C3;
}

.user__link {
  color: #ffffff;
  font-size: 0;
}

@media (min-width: 768px) {
  .user__link {
    width: auto;
    border-radius: 25px;
    padding-left: 50px;
    padding-right: 20px;
    font-family: "Raleway-semibold";
    font-size: 14px;
    line-height: 14px;
  }
  .user__link::before {
    left: 25px;
    transform: translate(0, -50%);
  }
}
.nav__list {
  list-style: none;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
}

.nav__link {
  color: #333333;
  font-weight: 500;
}
.nav__link:hover, .nav__link:active {
  color: #5BC3E3;
}

.nav__item {
  padding-top: 10px;
  padding-bottom: 10px;
}

@media (min-width: 1140px) {
  .nav__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
  }
  .nav__list-add {
    display: none;
  }
  .nav__item {
    padding-top: 0;
    padding-bottom: 0;
  }
}
.user-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.user-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.user-list__bottom {
  list-style: none;
  margin: 0;
  padding-bottom: 40px;
}
.user-list__bottom li {
  margin-bottom: 20px;
}

.user-list__link {
  font-family: "Raleway-semibold";
  font-size: 14px;
  line-height: 14px;
}
.user-list__link svg {
  margin-left: 5px;
  fill: #5BC3E3;
  stroke: #5BC3E3;
}
.user-list__link:hover svg {
  fill: #ffffff;
  stroke: #ffffff;
}

.user-list__item--first .user-list__link {
  font-family: "Raleway-medium";
  color: rgba(51, 51, 51, 0.33);
  border-bottom: 1px solid rgba(51, 51, 51, 0.33);
}
.user-list__item--first .user-list__link:hover, .user-list__item--first .user-list__link:active {
  color: rgb(51, 51, 51);
  border-bottom: 1px solid rgb(51, 51, 51);
}

.user-list__bottom .user-list__item--second {
  margin-bottom: 30px;
}

@media (min-width: 1140px) {
  .user-list__link {
    display: flex;
    align-items: center;
  }
  .user-list__bottom {
    display: none;
  }
  .user-list li {
    margin-bottom: 0;
  }
  .user-list ul {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
  }
  .user-list > li:last-child {
    margin-left: 68px;
    margin-right: 15px;
  }
  .user-list > li:first-child {
    margin-left: 15px;
  }
  .user-list__item--first {
    position: relative;
    margin-right: 25px;
  }
  .user-list__item--first:last-child {
    margin-right: 0;
  }
  .user-list__item--second {
    margin-right: 10px;
  }
  .user-list__item--second:last-child {
    margin-right: 0;
  }
  .user-list__dropdown {
    position: relative;
  }
  .user-list__dropdown-menu {
    position: absolute;
    top: calc(100% + 10px);
    left: 0;
    width: 200px;
    padding-left: 10px;
    padding-right: 10px;
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.15);
    border-radius: 3px;
    z-index: 10;
  }
  .user-list__dropdown-menu a {
    color: #333333;
  }
  .user-list ul .user-list__dropdown-menu {
    display: none;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .user-list__dropdown:hover .user-list__dropdown-menu {
    display: block;
  }
  .user-list .user-list__item--second {
    margin-bottom: 0;
    margin-right: 20px;
  }
  .user-list .user-list__item--second:last-child {
    margin-right: 0;
  }
  .user-list__bottom .user-list__item--second {
    margin-bottom: 0;
  }
}
.slider .slick-next {
  width: 30px;
  height: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/icon-arrow-right.svg");
  background-color: #ffffff;
  border-radius: 50%;
}
.slider .slick-next::before {
  content: "";
}

.slider .slick-prev {
  width: 30px;
  height: 30px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/icon-arrow-left.svg");
  background-color: #ffffff;
  border-radius: 50%;
}
.slider .slick-prev::before {
  content: "";
}

.slider .slick-prev:hover, .slider .slick-prev:focus {
  background-image: url("../img/icon-arrow-left-active.svg");
}

.slider .slick-next:hover, .slider .slick-next:focus {
  background-image: url("../img/icon-arrow-right-active.svg");
}

.slider .slick-dots li {
  height: 7px;
  width: 7px;
}

.slider .slick-dots li button {
  height: 7px;
  width: 7px;
  padding: 0;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.5);
}
.slider .slick-dots li button::before {
  height: 7px;
  width: 7px;
}

.slider .slick-dots .slick-active button {
  background-color: #ffffff;
}

